
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { ReturnSupplierA4Component } from 'src/app/sales/components/print/return-supplier/return-supplier-a4/return-supplier-a4.component';
import { ReturnSupplierA5Component } from 'src/app/sales/components/print/return-supplier/return-supplier-a5/return-supplier-a5.component';
import { ReturnSupplierK80Col4Component } from 'src/app/sales/components/print/return-supplier/return-supplier-k80-col4/return-supplier-k80-col4.component';
import { ReturnSupplierK80Col3Component } from 'src/app/sales/components/print/return-supplier/return-supplier-k80-col3/return-supplier-k80-col3.component';
import { ReturnSupplierK57Col3Component } from 'src/app/sales/components/print/return-supplier/return-supplier-k57-col3/return-supplier-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { FunctionService } from 'src/app/sales/services/function.service';
import { VhBillType10DetailEdit, VhBillType10Edit } from '../bill-type10-interface';
@Component({
  selector: 'bill-type10-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent {
  @Input() url = '';
  invoice_detail: VhBillType10DetailEdit[] = []
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType10Edit
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore;
  printer: any;
  listBarcodeLot = [];
  subTotal = 0
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private functionService: FunctionService,
    private vhQuery: VhQuery
  ) {
    this.getDataInvoice(this.router.getCurrentNavigation().extras.state.id)

    let print = this.vhQuerySales.getlocalPrinters().find(item => item.desktop)
    this.printer = print ? print.desktop.purchase[print.desktop.purchase.type] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
  }

  /**
   * gn bill v bill-detail get c vo bin v get cc thng tin ca nv, khch hng, phng thc thanh ton
   * @example this.init(bill, bill-deails)
   */
  init(invoice: VhBillType10Edit = null, invoiceDetail: VhBillType10DetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQuerySales.getlocalSupplier(this.invoice.id_supplier),
        paymentName: this.setPaymentName(this.invoice.payment_type)
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }
  /**
   * tnh cng tin ha n
   * @example this.setSubTotal()
   */
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
  }


  /**
   * get d liu bill t DB
   * @param id : nhn t state truyn qua
   * @example this.getDataInvoice('DHIWHNXDIWODUHIWDHOWID')
   */
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(id),
        this.vhQuerySales.getBill_details_byId_bill(id),
        this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, type: { $eq: 5 } })
      ]).then(([invoice, invoiceDetail, list_barcode]: any) => {
        this.listBarcodeLot = list_barcode.map(item => { item.property = this.vhAlgorithm.sortNumberbyASC(item.property, 'index'); return { ...item } })

        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }

        this.vhComponent.hideLoading(0)
      })
    })

  }

  /**
   * tr v tn unit ca sn phm theo ratio truyn vo
   * @param units 
   * @param ratio 
   * @returns string
   * @example let unit_name = this.getUnit([object], 1)
   */
  getUnit(units: any[], ratio) {
    return this.vhQuerySales.getUnit_byRatio(units, ratio).unit
  }

  /**
   * tr v chui thng tin l theo id l truyn vo
   * @param lots 
   * @param _id 
   * @returns string
   * @example let lot = this.getLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number
  }

  /**
   * tr v chui ngy sx - ngy ht hn ca ltheo id l truyn vo
   * @param lots 
   * @param _id 
   * @returns string
   * @example let date_lot = this.getDateLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getDateLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return this.functionService.formatDate(lot.date_mfg) + ' - ' + this.functionService.formatDate(lot.date_exp)
  }
  /**
   * xa ha n ln DB
   * @example this.deleteInvoice()
   */
  deleteInvoice() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        if (this.vhAuth.checkMyPermission("purchase_enable_cancel_bill")) {
          this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.languageService.translate("Cancel"))
            .then(() => {
              this.vhComponent.showLoading("", "transparent-loading").then(() => {
                this.vhQuerySales.deleteBill_Billdetail(this.invoice._id).then(() => {
                  this.vhComponent.hideLoading(0).then(() => {
                    this.vhComponent.showToast(2000,
                      `${this.languageService.translate("Purchase invoice")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                    this.goBack();
                  })
                })
              })
            }, () => { })
        }
        else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.functionService.formatDate(validitydate)}`, 5000)
      }
    })

  }
  /**
   * tr v tn phng thc thanh ton
   * @param type 
   * @returns string
   * @example this.setPaymentName(1)
   */
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQuerySales.getlocalWallet(this.invoice.id_wallet).name
    }
  }
  /**
   * tr v chui thng tin a ch ca supplier
   * @param partner 
   * @returns string
   * @example let addr = this.renderAddress(supplier_info)
   */
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  public goToCart() {
    this.vhQuerySales.getValidityDate(this.invoice.date).then((validitydate: any) => {
      if (validitydate == null) {
        this.router.navigate([this.url + '/edit-return-supplier'], { state: { invoiceDetail: this.invoice_detail, invoice: this.invoice, dataRestore: this.dataRestore } })
      }
      else {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.functionService.formatDate(validitydate)}`, 5000)
      }
    })

  }

  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_return_supplier')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  /**
   * kim tra thng tin d liu trang in v m component in
   * @example this.printInvoice()
   */
  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice_detail: this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => { })
        })
    }, (err) => this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
      .then(() => { })
    )
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReturnSupplierK57Col3Component
      case 'print_size_k80_3c': return ReturnSupplierK80Col3Component
      case 'print_size_k80_4c': return ReturnSupplierK80Col4Component
      case 'print_size_a5': return ReturnSupplierA5Component
      case 'print_size_a4': return ReturnSupplierA4Component
      default: return ReturnSupplierA4Component
    }
  }
  goBack() {
    this.router.navigate([this.url], { state: this.dataRestore });
  }
  ngAfterViewChecked() {
    if (document.querySelector("#purchase-invoice-detail-today") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-detail-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#purchase-invoice-detail-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-detail-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }

  product_selected: any = {};
  start_print = false;
  data_print: any;

  /**
   * in tem l ca sn phm c chn
   * @param item 
   * @example this.printLabel(product_selected)
   */
  printLabel(item) {
    let product = this.vhQuerySales.getlocalDetailProduct(item.id_subproduct ? item.id_subproduct : item.id_product)
    let lot_data = product.lots.find(e => e._id == item.id_lotproduct)
    this.product_selected = product
    let barcode_selected = this.listBarcodeLot.find(e => e._id == product.id_design_barcode_lot)
    if (barcode_selected) {
      this.start_print = true;
      this.data_print = {
        formProduct: {
          name: this.product_selected.name,
          ...lot_data
        },
        barcode: barcode_selected,
        col: this.product_selected.number_column,
        number: this.product_selected.number_print
      }
      setTimeout(() => {
        this.start_print = false;
      }, 2000);
    }
    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Bn cha chn mu tem cho sn phm ny!"))
    }

  }

}
